		methods:{
			getLastInfo(){
				let that = this
				that.jyJPush.getLastPushInfo(result=> {
				    // console.log("lastPushInfo = " + JSON.stringify(result));
				    if (result.errorCode == 0) {
				        //  没有数据或者其他错误
				        return 0;
